	module.exports = {"data":{"markdownRemark":{"html":"<p>Dưỡng da là cả một quá trình rất quan trọng và đòi hỏi nhiều bước dưỡng. Và một trong số đó là tẩy tế bào chết (nôm na là tẩy da chết), nó không chỉ làm sạch các lỗ chân lông mà còn lấy đi các tế bào da chết trên mặt bạn. Khi bạn loại bỏ lớp da cũ thì làn da của bạn sẽ sáng hơn, các vết thâm sẽ mờ nhanh hơn và hấp thu dưỡng chất tốt hơn. Vì vậy, hôm nay mình sẽ review cho bạn sản phẩm tẩy tế bào chết IOPE Moisture Skin Soft Peeling Gel 100ml. Mình toàn đợi đến dịp sale của hãng để mua cho rẻ thôi.</p>\n<figure style=\"width: 200px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/iopepeeling-1-3d918da448436a854465c42a5b1e27a3-cec65.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 317px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"iopepeeling 1\" title=\"\" src=\"/static/iopepeeling-1-3d918da448436a854465c42a5b1e27a3-cec65.png\" srcset=\"/static/iopepeeling-1-3d918da448436a854465c42a5b1e27a3-2567a.png 300w,\n/static/iopepeeling-1-3d918da448436a854465c42a5b1e27a3-cec65.png 317w\" sizes=\"(max-width: 317px) 100vw, 317px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption></figcaption>\n</figure>\n<p>IOPE là một trong những thương hiệu hạng trung của tập đoàn mỹ phẩm hàng đầu của Hàn Amore Pacific, dựa trên chất chiết xuất từ thảo dược và công nghệ khoa học tiên tiến. Dòng sản phẩm của IOPE bao gồm các sản phẩm chăm sóc da và mỹ phẩm cho cả nam và nữ.</p>\n<p><strong>Hãng giới thiệu</strong>:</p>\n<ul>\n<li>Thành phần Prebiotic  duy trì cân bằng làn da để tăng cường sức đề kháng tự nhiên của da khỏi các chất có hại.</li>\n<li>Nước hazel có tác dụng làm se dịu, nhẹ nhàng ngay cả trên da nhạy cảm.</li>\n<li>Chiết xuất cellulose tự nhiên từ cây xanh và chiết xuất lô hội nhẹ nhàng tẩy tế bào da chết, làm da mềm mại mà không bị khô.</li>\n</ul>\n<h2 id=\"thành-phần\"><a href=\"#th%C3%A0nh-ph%E1%BA%A7n\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Thành phần</h2>\n<p><span style=\"color:royalblue\"> Water, Cellulose, Alcohol, Dipropylene Glycol, Butylene Glycol, Glycerin, Camellia Sinensis Leaf Extract,Aloe Barbadensis Leaf Extract, Hamamelis Virginiana (Witch Hazel) Leaf Extract, Algae Extract, Corallina Officinalis Extract, Natto Gum, Beta-Glucan, Lactose, Microcrystalline Cellulose, Betaine, Sodium Hyaluronate, Sucrose, Silica, Alpha-Glucan Oligosaccharide, Ethylhexylglycerin,Zea Mays (Corn) Starch, Xanthan Gum, Carbomer, Tocopheryl Acetate, Phosphatidylcholine, Propanediol, Peg-60 Hydrogenated Castor Oil, Hydrogenated Lecithin, Potassium Hydroxide, Disodium Edta, Phenoxyethanol, Fragrance, Ultramarines (Ci 77007) </span></p>\n<h2 id=\"cách-dùng\"><a href=\"#c%C3%A1ch-d%C3%B9ng\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Cách dùng</h2>\n<p>Sau khi làm sạch da bằng sữa rửa mặt và vỗ nhẹ nước trên da, lấy một lượng nhỏ vào lòng bàn tay, xoa nhẹ nhàng trên toàn bộ khuôn mặt, và rửa sạch nó bằng nước ấm (cẩn thận không kích thích da quá nhiều bằng cách chà xát mạnh tay nhé).</p>\n<p>Mình sử dụng nó hai lần mỗi tuần. Sau khi tẩy da chết, da của bạn sẽ rất sạch nhưng cũng sẽ bị khô đấy, vì vậy bạn có thể dùng sheet mask sau mỗi lần tẩy da, dưỡng chất sẽ hấp thụ sâu vào làn da của bạn và mang lại hiệu quả tốt. Hơn nữa, tùy thuộc vào loại da mà bạn có thể chọn tần số để tẩy da chết, mặc dù mình nghĩ ngay cả với da nhờn, 3 lần mỗi tuần là đủ lắm rồi.</p>\n<h2 id=\"khi-mình-dùng-nè\"><a href=\"#khi-m%C3%ACnh-d%C3%B9ng-n%C3%A8\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Khi mình dùng nè</h2>\n<figure style=\"width: 700px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/iopepeeling-2-04cbd27fd24a73513f8a23ffce2617e1-dfb2c.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 1077px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 30.547818012999073%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"iopepeeling 2\" title=\"\" src=\"/static/iopepeeling-2-04cbd27fd24a73513f8a23ffce2617e1-dfb2c.png\" srcset=\"/static/iopepeeling-2-04cbd27fd24a73513f8a23ffce2617e1-dc28f.png 300w,\n/static/iopepeeling-2-04cbd27fd24a73513f8a23ffce2617e1-5c3b9.png 600w,\n/static/iopepeeling-2-04cbd27fd24a73513f8a23ffce2617e1-dfb2c.png 1077w\" sizes=\"(max-width: 1077px) 100vw, 1077px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption></figcaption>\n</figure>\n<p><strong>Ưu</strong></p>\n<ul>\n<li>Làm sạch sâu</li>\n<li>Làm trắng và mịn da ngay sau khi dùng</li>\n<li>Tẩy da chết hiệu quả</li>\n<li>Da ẩm, không bị khô</li>\n<li>Tuýp sản phẩm chắc tay, sạch sẽ khi dùng</li>\n<li>Dùng được trong một thời gian dài</li>\n<li>Mùi thơm rất nhẹ, không gây khó chịu (xin lỗi nhé, mình tệ nhất là khoảng tả mùi)</li>\n</ul>\n<p><strong>Khuyết</strong></p>\n<ul>\n<li>Giá đắt</li>\n<li>Tác dụng làm trắng tạm thời</li>\n</ul>\n<p>Nhìn chung, ngoài việc giá cao thì mình rất thích tẩy da chết của IOPE. Nhưng mà thật ra xài được lâu lắm, mà chât lượng khỏi chê. Mỗi lần xài xong là thấy da mình ngay lập tức mịn và sáng hơn hẳn, nó cũng không làm khô da nữa. Rõ rệt nhất là lúc trang điểm vào sáng hôm sau, quét cái gì lên mặt cũng thuận lợi, không bị mốc hay khô da tí nào. Bạn nào có điều kiện thì cứ tiến tới thôi, sống là phải hưởng thụ nhé.</p>","timeToRead":3,"excerpt":"Dưỡng da là cả một quá trình rất quan trọng và đòi hỏi nhiều bước dưỡng. Và một trong số đó là tẩy tế bào chết (nôm na là tẩy da chết), nó…","frontmatter":{"title":"Review - Tẩy tế bào chết IOPE Moisture Skin Soft Peeling Gel","cover":"/images/iopepeel.png","date":"2017/04/21","category":"Beauty","tags":["Beauty","skin-care","iope","peeling-gel","skincare","tay-da-chet","korean","cosmetic","my-pham-han-quoc","duong-da"]},"fields":{"slug":"/vi/Review-IOPE-Moisture-Skin-Soft-Peeling-Gel/"}}},"pathContext":{"path":"/vi/Review-IOPE-Moisture-Skin-Soft-Peeling-Gel/","langKey":"vi"}}

